 opacity: 0.8;\n    background-color: @black;\n    z-index: @zindexBlockInteraction;\n\n    > div {\n        @imageSize: 32px;\n        @padding: 8px;\n        @size: @imageSize + @padding * 2;\n\n        opacity: 1;\n        position: fixed;\n        top: calc(~\"50% - \" @size / 2);\n        left: calc(~\"50% - \" @size / 2);\n        .box-sizing(border-box);\n        width: @size;\n        height: @size;\n        .border-radius(4px);\n        background-color: @white;\n        padding: @padding;\n\n        > div {\n            width: @imageSize;\n            height: @imageSize;\n            background-image: url('../images/ajax-loader.gif');\n        }\n    }\n}\n\n// Full-screen modal for displaying form XML\n.fd-full-screen-modal {\n    .modal-dialog {\n        position: fixed;\n        left: 0;\n        right: 0;\n        top: 0;\n        bottom: 0;\n        margin: 0;\n        width: auto;    // override bootstrap's static width\n\n        .modal-content {\n            position: absolute;\n            @padding: 20px;\n            top: @padding;\n            bottom: @padding;\n            left: @padding;\n            right: @padding;\n\n            .modal-body {\n                overflow-y: auto;   // fixed position means scrolling happens within modal\n            }\n        }\n    }\n}\n","// Styles for multimedia attached to questions\n@import \"lib/main\";\n\n// Vellum wrapper\n.formdesigner .fd-ui-container {\n\n    // Area displaying small version of image\n    .fd-mm-preview-container {\n        text-align: center;\n        width: 115px;\n        float: left;\n\n        img, .fd-mm-no-ref {\n            border: 1px solid @grayLighter;\n        }\n\n        img {\n            padding: .5em;\n            background-color: @white;\n            margin-bottom: @gridGutterWidth/2;\n        }\n\n        .fd-mm-no-ref {\n            color: @grayLight;\n            background-color: @white;\n            padding: @gridGutterWidth/2;\n\n            i {\n                display: block;\n                font-size: @baseFontSize*4;\n            }\n        }\n    }\n\n    // Container for buttons (upload & show path)\n    .fd-mm-upload-container {\n        margin-left: 125px;\n\n        > .control-row {\n            margin: 0px 0px 10px 0;\n            &:first-child { height: 25px; }\n        }\n\n        .fd-mm-path-input {\n            margin-right: 92px;\n        }\n    }\n}\n","// Styling for popovers for data node bubbles\n.rich-text-popover {\n    .border-radius(3px);\n    word-wrap: break-word;\n\n    // Static width, haven't found a better way to do this\n    min-width: 236px;\n    max-width: 276px;\n\n    .popover-title {\n        h3 {\n            font-weight: 600;\n            font-size: 14px;\n            margin: 0;\n        }\n\n        .text-muted {\n            margin-top: 5px;\n            line-height: 1.3em;\n        }\n    }\n\n    // Offset the popover to account for the popover arrow\n    &.top, &.right, &.bottom, &.left {\n        margin: 0;\n    }\n    &.bottom { margin-top: 5px }\n    &.top { margin-bottom: -10px }\n    &.right { margin-left: 5px }\n\n    .arrow,\n    .arrow:after {\n        display: none;\n    }\n}\n\n.popover-content {\n    .alert {\n        margin-bottom: 0;\n    }\n}\n","// Styles for question properties content displayed in right column\n@import \"lib/main\";\n\n// Vellum wrapper\n.formdesigner .fd-ui-container {\n\n    // Question type changer and delete button\n    .fd-props-toolbar {\n        margin-bottom: 5px;\n        border-bottom: 1px solid @navbarBackground;\n        padding: @contentPaddingVellum;\n\n        .fd-question-comment {\n            margin-top: 10px;\n            white-space: pre-line;\n        }\n    }\n\n    // Main content (scrolling area, accordion of Basic, Logic, etc.)\n    .fd-props-content {\n        padding: 0 @contentPaddingVellum;\n\n        // Help icons\n        .fd-help {\n            width: 25px;\n            font-size: @baseFontSize * 1.5;\n\n            a:hover {\n                text-decoration: none;\n                color: @green;\n            }\n        }\n\n        // Copy-paste help (displayed in right-hand panel when multiple questions are selected in tree)\n        .fd-copy-paste-help {\n            .row {\n                padding: 0 2em;\n                margin-bottom: 2em;\n            }\n\n            .copy-paste-box textarea {\n                width: 100%;\n                height: 100px;\n            }\n        }\n\n        // Single accordion section (Basic, Logic, etc.)\n        .fd-question-fieldset {\n            position: relative;\n            min-width: 0px; /* for chrome */\n            display: table-column; /* for firefox */\n\n            // Always show main/basic section, but hide header\n            &:first-child {\n                margin-top: 20px;\n                legend {\n                    display: none;\n                }\n            }\n\n            // Header\n            legend {\n                min-height: 30px;   // tall enough for help icon\n\n                // Position section-level help at right of header\n                .fd-help {\n                   right: 0;\n                   top: 0;\n                }\n            }\n\n            // Accordion content\n            .fd-fieldset-content {\n\n                // Label for single input\n                .control-label {\n\n                    // Align help icon with top of input, and display inline with label\n                    .fd-help {\n                        position: relative;\n                        top: -8px;\n                        height: 15px;   // artifically small height to account for negative top\n                        display: inline-block;\n                    }\n\n                    // Pull text label up so it aligns with help icon\n                    label {\n                        vertical-align: top;\n                    }\n                }\n\n                .messages .alert {\n                    margin: 10px 0 0 0;\n                }\n\n                // Blocks of itext, like when adding multimedia or advanced > other content\n                .itext-block-container {\n                    .btn-danger {\n                        margin: 10px;\n                    }\n                }\n            }\n\n            // \"auto?\" checkbox for custom itext IDs\n            .fd-itextID-checkbox-container {\n                width: 55px;\n                padding: 0 5px;\n                background-color: lighten(@green, 50);\n                .border-radius(5px);\n            }\n\n            // Key-value section, as used in android intent questions\n            .fd-kv {\n                margin-bottom: 0;\n\n                .fd-kv-arrow {\n                    text-align: center;\n                    i {\n                        margin-top: 8px;\n                    }\n                }\n            }\n        }\n    }\n} \n","// Styling for the vellum container and its main content blocks: question tree, question properties panel, etc.\n@import \"lib/main\";\n\nbody.vellum-full-screen {\n    overflow-y: hidden;\n}\n\n// Wrapper around all of vellum, gets no styling\n.formdesigner {\n\n    // Another wrapper around all of vellum, can be styled (say, for full-screen mode)\n    .fd-ui-container {\n        top: 0;\n        width: 100%;\n        background-color: white;\n\n        &.full-screen {\n            z-index: @zindexUIContainer;\n        }\n\n        // Major content block (column or divider)\n        .fd-content {\n            float: left;\n\n            // Left column: question tree & data tree\n            &.fd-content-left {\n                width: 30%;\n\n                .fd-head h2 {\n                }\n\n                .fd-add-question {\n                    width: 100%;\n                    border-radius: 0;\n                    text-align: left;\n                    border: 0;\n\n                }\n\n                // Bottom half of left column: data tree\n                .fd-accessory-pane {\n                    overflow-x: hidden;\n                    overflow-y: auto;\n                }\n            }\n\n            // Right column: question properties or expression editor\n            &.fd-content-right {\n                .fd-head {\n                    height: 31px;\n\n                    h2 {\n                        line-height: 30px;\n                    }\n                }\n\n                // Tweak bootstrap's well styles\n                .well {\n                    padding: 10px;\n\n                    // Vellum's wells use col-sm-1 columns to squeeze in small items that usually have enough padding\n                    .col-sm-1 {\n                        padding: 0;\n                    }\n                }\n\n                // Bar with save button\n                .fd-form-actions {\n                    background-color: @cc-brand-mid !important; // lazy\n                    padding: 5px 8px 8px;\n                }\n            }\n\n            // Intro content for any panel (\"New Form, please add questions above...\")\n            .fd-default-panel {\n                padding-left: @contentPaddingVellum;\n            }\n\n            // Heading for any panel (question tree, expression editor, etc.)\n            .fd-head {\n                @menuContainerWidth: 50px;\n                color: @white;\n                background-color: @cc-brand-low;\n                font-size: 17px;    // size icons\n                height: @treeBrowserHeadHeight;\n                position: relative;\n\n                // Icon to expand/collapse panel\n                .fd-head-max-indicator {\n                    position: absolute;\n                    right: 10px;\n\n                    i {\n                        color: @cc-neutral-hi;\n                    }\n                }\n\n                // Light up expand/collapse icon when mouseover header\n                &:hover .fd-head-max-indicator i {\n                    color: @white;\n                }\n\n                > h2 {\n                    font-size: @baseFontSize * 0.9;\n                    padding: 1px @contentPaddingVellum 0;\n                    margin: 0;\n                    font-weight: normal;\n                    line-height: 23px;\n                    .text-overflow();\n                    position: absolute;\n                    left: 0;\n                    right: @menuContainerWidth - @contentPaddingVellum;\n                }\n\n                // Question tree menu (language switcher, copy, paste, etc.)\n                .fd-head-menu-container {\n                    position: absolute;\n                    right: 0px;\n                    top: 0px;\n                    line-height: 23px;\n                    font-size: @baseFontSize * 0.9;\n                    min-width: @menuContainerWidth;\n\n                    // Icon link to open dropdown\n                    .dropdown {\n                        .dropdown-toggle {\n                            color: @white;\n                            cursor: pointer;\n                            display: block;\n                            padding: 0 @contentPaddingVellum;\n                            text-decoration: none;\n                            background-color: @cc-brand-low;\n\n                            &:hover {\n                                text-decoration: none;\n                                background-color: @cc-brand-mid;\n                            }\n                        }\n\n                        // Menu options\n                        .dropdown-menu {\n                            a {\n                                min-width: 170px;\n\n                                > .hotkey {\n                                    float: right;\n                                }\n                            }\n                        }\n                    }\n                }\n            }\n        }\n\n        // Draggable separators between .fd-content blocks\n        .fd-content-vertical-divider {\n            width: 5px;\n            background: @cc-brand-mid;\n        }\n\n        .fd-content-horizontal-divider {\n            height: 5px;\n            margin: 1px 0;\n            background: @navbarBackground;\n            background-repeat: no-repeat;\n            background-position: center center;\n            background-image: url('../images/ellipsis.png');\n            background-size: auto 80%;\n        }\n\n        // Vellum-level messages displayed across bottom of screen\n        .fd-messages {\n            position: fixed;\n            bottom: 40px;\n            left: 0;\n            width: 100%;\n\n            .alert {\n                .box-shadow(0px 0px 10px rgba(0, 0, 0, 0.4));\n                margin: @gridGutterWidth;\n                height: auto;\n                overflow: auto;\n                max-height: 200px;\n            }\n        }\n\n        // Let major content blocks scroll independently (i.e., tree versus expression editor)\n        // .fd-scrollable is for containers meant to take the full screen height (e.g., question properties panel)\n        // .fd-scrollable-minimal is for containers that should not grow taller than their content (e.g., menus)\n        .fd-scrollable, .fd-scrollable-minimal {\n            overflow-x: hidden;\n            overflow-y: auto;\n\n            // Prevent scrollbars underneath the modal from bleeding through on OSX\n            transform: translate3d(0, 0, 0);\n        }\n\n        .fd-scrollable {\n            height: @containerHeight;\n            width: 100%;    // If applied to .fd-scrollable-minimal too, this makes their content also scroll horizontally\n        }\n\n        .fd-scrollable-minimal {\n            max-height: @containerHeight;\n        }\n    }\n}\n","// Styling for trees (question tree and external data tree)\n@import \"lib/main\";\n\n// Vellum wrappers\n.formdesigner .fd-ui-container {\n\n    // Styles shared by both trees\n    .jstree {\n\n        .jstree-node {\n\n            // Items in tree (type icon + name)\n            .jstree-anchor {\n                color: darken(@blueDark, 10);\n                line-height: 27px;\n                height: 26px;\n\n                &.jstree-hovered,\n                &.jstree-clicked {\n                    border: none;\n                    background: desaturate(lighten(@blue, 58), 40);\n                }\n\n                &.jstree-clicked {\n                    .box-shadow(0px 0px 4px fade(@blue, 60%));\n                }\n            }\n\n            .jstree-anchor > .jstree-icon, .add_choice:before {\n                background: none;\n                font-size: .85em;\n                color: @cc-brand-mid;\n            }\n\n            .add_choice {\n                display: block;\n                margin-left: 50px;\n                cursor: pointer;\n                padding: 0 5px;\n\n                // This is fragile; it overwrites fontawesome styles with .jstree-anchor and body styles\n                font-family: \"Helvetica Neue\", Helvetica, Arial, sans-serif;\n                line-height: 24px;\n                height: 24px;\n\n                &:before {\n                    margin-right: 6px;\n\n                    // Bring back .fa font\n                    font: normal normal normal 14px/1 FontAwesome;\n                }\n\n                // Also fragile; both because of the static width and because the other styles\n                // are taken from .jstree-hovered\n                &:hover {\n                    border: none;\n                    background-color: #eaeff5;\n                    width: 95px;\n                }\n            }\n\n            &.jstree-closed .add_choice {\n                display: none;\n            }\n\n            .fd-valid-alert-icon {\n                margin-right: 3px;\n                margin-left: -3px;\n            }\n        }\n    }\n\n    // Styles for question tree only, including header\n    .fd-tree {\n        .fd-head {\n            @height: 46px;\n            height: @height;\n\n            h2 {\n                line-height: @height;\n                font-size: 14px;\n            }\n\n            .fd-head-menu-container {\n                line-height: @height;\n            }\n        }\n\n        .fd-scrollable {\n            overflow-x: auto;\n\n            // Separate tree from header\n            .jstree {\n                margin: 5px 0;\n            }\n        }\n\n        .fd-undo-delete {\n            margin-bottom: 0px;\n        }\n    }\n\n    // Panel for external data sources tree, including header\n    .fd-external-sources-container {\n\n        // Header for panel\n        .fd-head.fd-head-external-sources {\n            cursor: pointer;\n\n            h2 {\n                font-size: 12px;\n            }\n        }\n\n        // Area for search\n        .fd-external-resource-search {\n            background-color: @cc-bg;\n            margin-bottom: 5px;\n            > form {\n                position: relative;\n                padding: 0px @contentPaddingVellum;\n                i {\n                    line-height: 42px;\n                }\n            }\n\n            // Icon and search box\n            .fd-search-control {\n                position: absolute;\n                top: 5px;\n                margin-left: 18px;\n                width: 100%;\n            }\n        }\n\n        // Data tree itemsuse a different color scheme\n        .fd-external-sources-tree .jstree-anchor {\n            color: @cc-neutral-low;\n            &.jstree-clicked {\n                background-color: @cc-bg;\n                .box-shadow(0px 0px 4px fade(@cc-neutral-mid, 80%));\n            }\n\n            > .jstree-icon {\n                color: @cc-neutral-mid;\n            }\n        }\n    }\n\n    .fd-undo-delete p {\n        margin-top: 1em;\n    }\n}\n\n#jstree-dnd {\n    max-width: 300px;\n    .text-overflow();\n}\n"],"sourceRoot":""}]);
